:root {
  --spacing-sm: 1rem;
  --spacing-md: 2rem;
  --spacing-lg: 3rem;
  --spacing-xl: 4rem;

  --body-bg: #0d0c22;
  --elements-bg: #2c2b50;

  --text-color: rgba(255, 255, 255, 0.72);
  --headings-color: rgba(255, 255, 255, 0.9);

  --light-blue: #e0e7ff;

  --light-indigo: #818cf8;
}

body {
  font-family: "helvetica";
  background-color: var(--body-bg);
  line-height: 1.5;
}
p {
  color: var(--text-color);
}
h1,
h2,
h3 {
  color: var(--headings-color);
}
h1 {
  font-size: 3rem;
  line-height: 1.1;
  background: linear-gradient(
    159deg,
    hsl(234deg 89% 82%) 27%,
    rgba(67, 56, 202, 1) 97%
  );
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
